<template>
  <div>
     <Spin size="large" fix>
       <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
                <div>正在授权，请稍后...</div>
     </Spin>
  </div>
</template>
<script>
import qs from 'qs';
export default {
  data() {
    return {
    	params:{}
    }
  },
  mounted() {
    let str = location.search.slice(1);
    let obj = qs.parse(str);
    this.postAuthCode(obj.auth_code)
  },
  methods:{
    postAuthCode(code) {
      this.$http.post(this.$api.wxa_authcode,{authcode:code}).then(res=>{
        this.$Message.success('授权成功')
        setTimeout(()=>{
          location.href = location.origin + location.pathname + '#/weixin/wxaconfig'
        },1000)
      }).catch(erro=>{
        this.$Message.error('授权失败');
        console.log(erro)
        setTimeout(()=>{
          location.href = location.origin + location.pathname +'#/weixin/wxaconfig'
        },1000)
        
      })
    },
  },
}

</script>
<style>


</style>


